html {
  width: auto;
}
body {
  background-color: #111111;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  /* border: 1px solid red; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  font-family: "Arvo", serif;
  height: 100vh;
  position: relative;
}

.leftBorder {
  width: 75px;
  background-color: #1d1e20;
  position: fixed;
  left: 0;
  height: 100%;
}

.pageContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 60px 100px 60px 95px;
}

@media (max-width: 767px) {
  .leftBorder {
    display: none;
  }
}

@media (max-width: 560px) {
  .pageContainer {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: auto;
  }
}

.mainContent p {
  color: #ffffff;
  opacity: 0.3 !important;
  position: absolute;
  left: 30px;
  font-size: 18px !important;
}

.mainContent {
  color: white;
  font-size: 40px;
}

.name {
  background: linear-gradient(127.76deg, #0dfc9c 0%, #00bdc0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.mainContent a {
  color: #ffffff;
  opacity: 0.5;
  margin: 0 10px;
  cursor: pointer;
  text-decoration: none !important;
}

.indent {
  padding-left: 60px;
  font-size: 30px !important;
}

.line3 {
  color: #ffffff;
}

.links {
  opacity: 0.5;
}

.mainPic {
  border-radius: 50%;
  width: 130px;
  padding: 25px 20px;
}

.padding-bottom {
  padding-bottom: 20px;
}

@media (max-width: 767px) {
  .mainContent {
    margin-left: 55px;
    padding-left: 15px;
    overflow: hidden;
  }
  .mainContent p {
    display: none;
  }
  .mainContent {
    font-size: 18px !important;
    position: fixed;
    bottom: 5%;
  }
  .indent {
    padding-left: 10px;
    font-size: 14px !important;
  }
  .links {
    opacity: 0.5;
    display: flex;
    flex-direction: column;
  }
  .links a {
    margin-bottom: 5px;
  }
  .mainPic {
    border-radius: 50%;
    width: 80px;
    padding: 25px 20px;
  }
  .padding-bottom {
    padding-bottom: 5px;
  }
}

.workTile {
  height: 450px;
  display: flex;
  justify-content: space-around;
  margin: 160px 0px;
}

.workTile .leftContent {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 60px 0px;
  width: 30%;
}

.rightContent {
  position: relative;
  margin-right: 250px;
}

.superTitle {
  font-size: 20px;
}

.title {
  width: 50%;
  font-size: 60px;
}

.built {
  font-size: 12px;
  font-weight: bold;
  opacity: 0.4;
  font-family: "Roboto", sans-serif !important;
}

.techSection {
  display: flex;
}

.tech {
  margin-right: 20px;
  font-size: 12px;
  font-weight: bold;
  font-family: "Roboto", sans-serif !important;
}

.box {
  width: 450px;
  height: 450px;
  /*background-color: #D8D8D8;*/
}

.button {
  position: absolute;
  bottom: 45px;
  left: -150px;
  width: 200px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(127.76deg, #0dfc9c 0%, #00bdc0 100%);
}

.rightContent a {
  font-size: 18px;
  text-decoration: none;
  color: #ffffff;
}

@media (max-width: 767px) {
  .box {
    display: none;
  }
  .workTile {
    margin: 0;
    height: auto;
    flex-direction: column;
    width: 80%;
  }
  .workTile .leftContent {
    width: auto;
    margin: 24px 0;
  }
  .superTitle {
    font-size: 14px;
  }
  .title {
    width: auto;
    font-size: 20px;
    margin-bottom: 10px;
  }
  .built {
    margin-bottom: 4px;
  }
  .button {
    position: relative;
    font-size: 16px;
    left: 0;
    bottom: 0;
    width: 120px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(127.76deg, #0dfc9c 0%, #00bdc0 100%);
  }
  .tech {
    margin-right: 10px;
    font-size: 10px;
  }
}

.videoFrame {
  max-width: 800px !important;
  width: 50vw !important;
}

.videoFrameMobile {
  max-width: 250px !important;
  width: 50vw !important;
}

.workPage {
  color: #ffffff;
  height: 100%;
}

@media (max-width: 767px) {
  .workPage {
    height: auto;
    margin-left: 45px;
    padding-left: 10px;
  }
}

.aboutPage {
  height: 90%;
  color: #ffffff;
  display: flex;
  justify-content: space-around;
  margin: 0px 150px;
}

.aboutPage .mainPic {
  width: 400px !important;
}

.header {
  display: none;
}

.aboutPage .superTitle {
  font-size: 50px;
  opacity: 0.5;
  font-family: "Arvo", serif;
}

.aboutPage .title {
  font-size: 80px;
  opacity: 1;
  font-family: "Arvo", serif;
  margin-bottom: 25px;
}

.description {
  font-family: "Roboto", sans-serif;
  line-height: 20px;
  margin-bottom: 15px;
}

.aboutPage .leftContent {
  margin: 225px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 30%;
}

.aboutPage .rightContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 0;
}

@media (max-width: 767px) {
  .aboutPage {
    margin: 0;
  }
  .aboutPage .rightContent {
    display: none;
  }
  .aboutPage .leftContent {
    margin: 0 0 0 45px;
    padding: 0 15px;
    width: auto;
    font-size: 14px;
    align-items: center;
    text-align: center;
  }
  .aboutPage .superTitle {
    display: none;
  }
  .aboutPage .title {
    display: none;
  }
  .header {
    display: block;
    margin: 0;
  }
  .mobile-superTitle {
    font-size: 30px;
  }
  .mobile-title {
    font-size: 50px;
    margin-bottom: 0;
  }
}

.yearLineContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.year {
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 1 !important;
    margin-bottom: 20px;
}

.line {
    box-sizing: border-box;
    width: 2px;
    border: 1px solid #FFFFFF;
    opacity: 0.5;
    margin-bottom: 20px;
}
.blockContainerLeft {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 140px;
}

@media (min-width: 2040px) {
  .blockContainerLeft {
    padding-left: 10%;
  }
  .blockContainerRight {
    padding-right: 10%;
  }
  .block {
    width: 30% !important;
  }
}

.blockContainerRight {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 140px;
}

.block {
  display: flex;
  flex-direction: column;
  width: 40%;
}

.image {
  margin-bottom: 35px;
  width: 90px;
  border-radius: 50px;
}

.role {
  opacity: 0.5;
  font-size: 16px;
  margin-bottom: 12px;
}

.company {
  font-size: 40px;
  margin-bottom: 12px;
}

.timespan {
  font-size: 14px;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  opacity: 0.5;
  margin-bottom: 30px;
}

.listContainer {
  padding-left: 15px !important;
}

.descList {
  font-size: 12px;
  font-weight: bold;
  font-family: Roboto, sans-serif;
  line-height: 20px;
}

@media (max-width: 767px) {
  .blockContainerLeft {
    margin-bottom: 40px;
  }
  .blockContainerRight {
    margin-bottom: 20px;
  }
  .block {
    width: auto;
  }
  .image {
    margin-bottom: 10px;
    width: 45px;
  }
  .role {
    font-size: 14px;
    margin-bottom: 4px;
  }
  .company {
    font-size: 28px;
    margin-bottom: 12px;
  }
  .timespan {
    font-size: 12px;
    margin-bottom: 0px;
  }
  .listContainer {
    font-size: 12px;
  }
}

.expPageContainer {
  color: white;
  height: 100%;
  position: relative;
  margin-top: 160px;
}

.timeline {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
}

@media (max-width: 767px) {
  .expPageContainer {
    margin: 25px 0px 0px 45px;
    padding-left: 10px;
  }
  .timeline {
    display: none;
  }
}

@-webkit-keyframes open-nav {
  from {
    left: -115px;
  }
  to {
    left: 0;
  }
}

@keyframes open-nav {
  from {
    left: -115px;
  }
  to {
    left: 0;
  }
}

@-webkit-keyframes close-nav {
  from {
    left: 0px;
  }
  to {
    left: -115px;
  }
}

@keyframes close-nav {
  from {
    left: 0px;
  }
  to {
    left: -115px;
  }
}

.navbar {
  width: 100%;
  display: flex;
  height: 30px;
  justify-content: flex-end;
  position: relative;
}

.side-nav {
  display: none;
}

.nameHome {
  background: linear-gradient(127.76deg, #0dfc9c 0%, #00bdc0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 1 !important;
  position: absolute;
  left: 0;
  margin-left: 0 !important;
}

.navbar a {
  margin-left: 45px;
  color: #ffffff;
  opacity: 0.5;
  font-size: 18px;
  cursor: pointer;
}

.navbar a:link {
  text-decoration: none;
}

.is-active {
  opacity: 1 !important;
}

@media (max-width: 767px) {
  .navbar {
    display: none;
  }

  .side-nav {
    background-color: #1d1e20;
    display: flex;
    flex-direction: column;
    height: 100%;
    left: -115px;
    padding: 14px 10px 0;
    position: fixed;
    top: 0;
    width: 140px;
    z-index: 1;
  }

  .side-nav a {
    margin-top: 16px;
    font-size: 14px;
    color: #ffffff;
    opacity: 0.5;
    cursor: pointer;
  }

  .side-nav a:link {
    text-decoration: none;
  }

  .side-nav.open-false {
    left: -115px;
    -webkit-animation: close-nav 0.5s;
            animation: close-nav 0.5s;
  }

  .side-nav.open-true {
    left: 0;
    -webkit-animation: open-nav 0.5s;
            animation: open-nav 0.5s;
  }

  .side-nav img {
    align-self: flex-end;
    width: 24px;
  }
}

.projectPageContainer {
  color: white;
  height: 100%;
}

.firstPage {
  min-height: 95vh;
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
}

.secondPage {
  padding-top: 100px;
}

.projectPageContainer .leftContent {
  display: flex;
  flex-direction: column;
}

.projectSuperTitle {
  font-size: 14px;
  font-weight: bold;
  opacity: 0.5;
  font-family: Roboto, sans-serif;
  margin-bottom: 10px;
}

.overview {
  font-family: Arvo, serif;
  font-size: 40px;
  margin-bottom: 30px;
}

.projectPageContainer .description {
  font-size: 12px;
  font-weight: bold;
  font-family: Roboto, sans-serif;
  line-height: 20px;
  margin-bottom: 0;
  width: 300px;
}

.projectPageContainer .rightContent {
  position: relative;
  margin-right: 0;
}

.desktopPic {
  width: 600px;
}

.mobilePic {
  width: 210px;
  position: absolute;
  bottom: -40px;
  right: -95px;
  box-shadow: 1px 1px 10px #666666;
}

.imageContainer {
  padding-bottom: 200px;
}

.simpleSlider {
  display: none;
}

@media (max-width: 767px) {
  .projectPageContainer {
    margin-left: 45px;
    padding-left: 10px;
    display: flex;
    align-items: center;
    text-align: center;
  }
  .firstPage {
    min-height: 0;
    justify-content: unset;
    align-items: unset;
    flex-direction: column;
    margin: 30px 0;
    align-items: center;
  }
  .projectPageContainer .rightContent {
    display: none;
  }
  .projectPageContainer .description {
    margin-bottom: 30px;
  }
  .overview {
    font-size: 36px;
  }
  .secondPage {
    display: none;
  }
  .simpleSlider {
    display: inline;
    display: initial;
  }
}

